import { socket } from "@/socket";

export default {
    name: "ConfigMatricula",
    data() {
        return {
            numMatricula: '',
        }
    },
    methods: {
        listenMatricula() {
            socket.on('matricula', (arg) => {
                this.numMatricula = arg;
                this.$store.commit('addMatricula', this.numMatricula)
                console.log(this.numMatricula);
            })
        },
    },
    mounted() {
        this.listenMatricula()
    },
};
